import React, { useState } from "react";
import {
  useConnectedWallet,
  useWallet,
  WalletStatus
} from "@terra-money/wallet-provider";
import { Chip, Zoom } from "@mui/material";
import Menu from "@mui/material/Menu";
import MenuItem from "@mui/material/MenuItem";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";
import KeyboardArrowRightIcon from "@mui/icons-material/KeyboardArrowRight";
import { truncate } from "../../utils/truncate";
import { PixyButton } from "../pixyComponents";

export const Wallet = () => {
  const wallet = useWallet();
  const connectedWallet = useConnectedWallet();

  console.log("wallet: ", wallet);
  console.log("connected wallet: ", connectedWallet);

  const [anchorEl, setAnchorEl] = useState<Element | null>(null);
  const open = Boolean(anchorEl);
  const handleClick = (event: any) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  const connectExtension = () => {
    wallet.connect(wallet.availableConnectTypes[1]);
    handleClose();
  };

  const disconnect = () => {
    wallet.disconnect();
    handleClose();
  };

  switch (wallet.status) {
    case WalletStatus.INITIALIZING:
      return <Chip color="primary" label="Loading..." variant="outlined" />;
    case WalletStatus.WALLET_CONNECTED:
      return (
        <>
          <PixyButton
            navButton
            upperCase={false}
            fullWidth
            id="basic-menu"
            aria-controls="basic-menu"
            aria-haspopup="true"
            aria-expanded={open ? "true" : undefined}
            onClick={handleClick}
            endIcon={<KeyboardArrowDownIcon />}
          >
            👤 {truncate(connectedWallet?.walletAddress || "", 12)}
            {" (" + (connectedWallet?.network.name || "")})
          </PixyButton>
          <Menu
            anchorEl={anchorEl}
            open={open}
            onClose={handleClose}
            MenuListProps={{ "aria-labelledby": "basic-button" }}
          >
            <MenuItem sx={{ color: "black" }} onClick={disconnect}>
              <KeyboardArrowRightIcon />
              Disconnect
            </MenuItem>
          </Menu>
        </>
      );
    case WalletStatus.WALLET_NOT_CONNECTED:
      return (
        <>
          <PixyButton
            navButton
            upperCase={false}
            fullWidth
            id="basic-menu"
            aria-controls="basic-menu"
            aria-haspopup="true"
            aria-expanded={open ? "true" : undefined}
            variant="contained"
            onClick={handleClick}
          >
            Connect Wallet
          </PixyButton>
          <Menu
            anchorEl={anchorEl}
            open={open}
            onClose={handleClose}
            PaperProps={{
              style: {
                background: "#280625"
              }
            }}
            MenuListProps={{ "aria-labelledby": "basic-button" }}
            sx={{ mt: 1 }}
            TransitionComponent={Zoom}
          >
            {/* TODO: use theme instead of sx.color */}
            <MenuItem sx={{ color: "#CCC" }} onClick={connectExtension}>
              <KeyboardArrowRightIcon /> Terra Station (extension/mobile)
            </MenuItem>
          </Menu>
        </>
      );
    default:
      return <span>Unexpected wallet status: {wallet.status}</span>;
  }
};
