import React from "react";
import { useTheme } from "@mui/material";
import Typography from "@mui/material/Typography";

export enum TYPOGRAPHY_METADATA_KIND {
  Price = "Price",
  NFTTitle = "NFTTitle"
}

const TypographyMetadata = ({
                              kind = TYPOGRAPHY_METADATA_KIND.Price,
                              center = false,
                              sx = {},
                              children,
                              // Any additional props will be passed to the
                              // underlying material-ui component.
                              ...rest
                            }) => {
  const { typography, palette } = useTheme();

  return (
    // @ts-ignore
    <Typography
      component="div"
      sx={{
        ...(kind === TYPOGRAPHY_METADATA_KIND.Price ? {
          textShadow: "0px 4px 4px rgba(0, 0, 0, 0.25)",
          lineHeight: "24px",
          color: palette.success.main
        } : {
          letterSpacing: "1.2px",
          color: palette.common.white,
          lineHeight: "30px"
        }),
        fontFamily: typography.fontFamily,
        fontWeight: 700,
        fontSize: 30,
        ...(center && {
          display: "flex",
          alignItems: "center",
          justifyContent: "center"
        }),
        ...sx
      }}
      {...rest}
    >
      {children}
    </Typography>
  );
};

export default TypographyMetadata;
