import React, { ReactNode } from "react";
import { Dialog, DialogContent } from "@mui/material";
import { PixyModalBackdrop } from "./PixyModal/PixyModalBackdrop";

export interface PixyModalProps {
  open: boolean;
  onClose: () => void;
  children: ReactNode;
}

export const PixyModal = ({
                            children,
                            open,
                            onClose,
                            ...rest
                          }: PixyModalProps) => {
  return (
    <Dialog
      scroll="body"
      PaperProps={{
        sx: {
          mt: 16,
          background: `linear-gradient(101.73deg, rgba(255, 255, 255, 0.05) -6.48%, rgba(0, 0, 0, 0.05) 106.74%),
            linear-gradient(0deg, rgba(87, 150, 209, 0.3), rgba(87, 150, 209, 0.3)),
            linear-gradient(0deg, rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0.3))`,
          boxSizing: "border-box",
          boxShadow: "10px 10px 30px 0px rgba(0, 0, 0, 0.25),0px 4px 4px 0px rgba(0, 0, 0, 0.25)",
          borderRadius: "5px",
          border: "1px solid rgba(255, 255, 255, 0.04)"
        }
      }}
      BackdropComponent={PixyModalBackdrop}
      open={open}
      onClose={onClose}
      fullWidth
      aria-labelledby="modal-modal-title"
      aria-describedby="modal-modal-description"
      maxWidth="md"
      {...rest}
    >
      <DialogContent className="flex flex-col items-center">
        {children}
      </DialogContent>
    </Dialog>
  );
};
