import React from "react";
import { Backdrop } from "@mui/material";

export const PixyModalBackdrop = (props: any) => {
  return (
    <Backdrop
      sx={{
        opacity: 0.85,
        filter: "drop-shadow(0px 4px 14px rgba(0, 0, 0, 0.8))"
      }}
      {...props}
    >
      <div
        className="w-full h-full"
        style={{
          background: "linear-gradient(81.42deg, rgba(255, 0, 199, 0.6) 0.43%, rgba(0,"
            + " 240, 255, 0.6) 97.99%)",
          backgroundBlendMode: "multiply"
        }}
      >
        <div
          className="w-full h-full"
          style={{
            background: "linear-gradient(90deg, rgba(0, 0, 0, 0.5) 11.6%, rgba(0, 0, 0, 0) 52.99%)",
            backgroundBlendMode: "darken"
          }}
        >
          <div
            className="w-full h-full"
            style={{
              backdropFilter: "blur(20px)",
              background: "rgba(0, 0, 0, 0.5)"
            }}
          />
        </div>
      </div>
    </Backdrop>
  );
};
