import React, { ReactNode } from "react";
import TextField from "@mui/material/TextField";
import Box from "@mui/material/Box";
import LocalizationProvider from "@mui/lab/LocalizationProvider";
import AdapterDateFns from "@mui/lab/AdapterDateFns";
import DatePicker from "@mui/lab/DatePicker";

export interface CreateCampaignProps {
  children: ReactNode;
}

// const Cards = styled("div")(({ theme: any }) => ({
//   display: "flex",
//   alignItems: "left",
//   justifyContent: "left",
//   height: "100%",
// }));

export const CreateCampaign = ({ children }: CreateCampaignProps) => {
  const [launchValue, setLaunchValue] = React.useState(null);
  const [completionValue, setCompletionValue] = React.useState(null);
  const Foo = () => {
    console.log("launchValue: ", launchValue);
    console.log("completionValue: ", completionValue);
    if (launchValue && completionValue) {
      return (
        Math.round((completionValue - launchValue) / (100 * 60 * 60 * 24)) /
        10 +
        " days"
      );
    } else {
      return "0 days"; // compute duration
    }
  };
  console.log("launchValue: ", launchValue);
  console.log("completionValue: ", completionValue);

  return (
    <>
      <h3>Start your Campaign</h3>
      <Box>
        <TextField id="outlined-basic" label="Name" variant="outlined" />
      </Box>
      <Box>
        <LocalizationProvider dateAdapter={AdapterDateFns}>
          <DatePicker
            label="Launch Date"
            value={launchValue}
            onChange={(newValue) => {
              setLaunchValue(newValue);
            }}
            renderInput={(params) => <TextField {...params} />}
          />{" "}
          <DatePicker
            label="Completion Date"
            value={completionValue}
            onChange={(newValue) => {
              setCompletionValue(newValue);
            }}
            renderInput={(params) => <TextField {...params} />}
          />{" "}
        </LocalizationProvider>
        <TextField
          id="outlined-basic"
          disabled
          value={Foo()}
          variant="outlined"
        />
      </Box>
    </>
  );
};
