import React, { ReactNode, useState, useEffect } from "react";
import { Link, useHistory } from "react-router-dom";
import { Wallet } from "../Wallet";
import { AppBar, List, ListItem, Toolbar, Typography } from "@mui/material";
import ArrowForwardRoundedIcon from "@mui/icons-material/ArrowForwardRounded";
import { styled } from "@mui/material/styles";
import {IconButton} from "@material-ui/core";
import MenuIcon from "@material-ui/icons/Menu";
import Box from "@mui/material/Box";
import {Drawer} from "@material-ui/core";
import pixyLogo from "../../assets/images/Pixy_Logo.png";
import { PixyButton, PixyNavLink } from "../pixyComponents";
import { BackgroundLayer } from "../BackgroundLayer/BackgroundLayer";

export interface HeaderProps {
  children: ReactNode;
}

const CenterSection = styled("div")(({ theme: any }) => ({
  margin: "0 auto",
  flexGrow: 1,
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  width: "100%"
}));

const RightSection = styled("div")(({ theme: any }) => ({
  marginLeft: 0,
  display: "flex",
  alignItems: "flex-end",
  width: "100%",
  justifyContent: "space-evenly",
  flexGrow: 1
}));
const pixyNavLinks = [
  {
    text: "Home",
    to: "",
    isHashLink: false
  },
  {
    text: "Catalog",
    to: "catalog",
    isHashLink: true
  },
  {
    text: "How It Works",
    to: "how-it-works",
    isHashLink: true
  },
  {
    text: "Farming",
    to: "farming",
    isHashLink: false
  },
  {
    text: "Governance",
    to: "governance",
    isHashLink: false
  }
];
export const Header = ({ children }: HeaderProps) => {
  const [state, setState] = useState({
    mobileView: false,
    drawerOpen: false,
  })
  const { mobileView, drawerOpen } = state;

  useEffect(() => {
    const setResponsiveness = () => {
      return window.innerWidth < 900
        ? setState((prevState) => ({...prevState, mobileView: true}))
        : setState((prevState) => ({...prevState, mobileView: false}))
    }

    setResponsiveness();
    window.addEventListener("resize", () => setResponsiveness());

    return () => {
      window.removeEventListener("resize", () => setResponsiveness());
    }
  }, []);
  const history = useHistory();

  const displayMobile = () => {
    const handleDrawerOpen = (e) => {
      e.preventDefault();
      setState((prevState) => ({ ...prevState, drawerOpen: true }));
    }
      
    const handleDrawerClose = () =>
      setState((prevState) => ({ ...prevState, drawerOpen: false }));
    return(
      <Toolbar
        variant="regular"
        sx={{
          width: "100%",
          background: "transparent",
          top: 20,
          height: 91,
          maxWidth: 1322,
          position: "absolute"
        }}
      >
        {/* <IconButton
          edge="start"
          color="inherit"
          aria-label="menu"
          sx={{ mr: 2 }}
          >
          <MenuIcon />
          </IconButton> */}
        <Typography variant="h6" color="inherit" component="div">
          <Link to="/">
            <img src={pixyLogo} height={25} alt="PIXY" width="50%" />
          </Link>
        </Typography>
        <button onClick={
          (e) => handleDrawerOpen(e)
        }>
          <MenuIcon style={{color: "white"}} />
        </button>
        <Drawer
          {...{
            anchor: "right",
            open: drawerOpen,
            onClose: handleDrawerClose,
            color: "black",
          }}
        >
          <Box>{getDrawerChoices()}</Box>
        </Drawer>
      </Toolbar>
    );
  };

  const getDrawerChoices = () => {
      return (
        <>
          <CenterSection sx={{display: "block", width: "73%"}}>
            <List>
              {pixyNavLinks.map(
                ({
                    to,
                    text,
                    isHashLink
                  }: { to: string; text: string; isHashLink: boolean }) => (
                  <ListItem key={to} style={{ whiteSpace: "nowrap" }}>
                    <PixyNavLink
                      isHashLink={isHashLink}
                      to={`/${!isHashLink ? "" : "#"}${to}`}
                    >{text}</PixyNavLink>
                  </ListItem>
                )
              )}
            </List><br></br>
            <Wallet /><br></br><br></br>
            <PixyButton
              onClick={() => history.push("/create")}
              navButton
              fullWidth
              endIcon={<ArrowForwardRoundedIcon />}
            >
              Launch Project
            </PixyButton>
          </CenterSection>
        </>
      );
  };

  const displayDesktop = () => {
    console.log("desktop");
    return(
      <Toolbar
        variant="regular"
        sx={{
          width: "100%",
          background: "transparent",
          top: 20,
          height: 91,
          maxWidth: 1322,
          position: "absolute"
        }}
      >
        <Typography variant="h6" color="inherit" component="div">
          <Link to="/">
            <img src={pixyLogo} height={25} alt="PIXY" />
          </Link>
        </Typography>
        {children}
        <CenterSection>
          <List className="flex">
            {pixyNavLinks.map(
              ({
                  to,
                  text,
                  isHashLink
                }: { to: string; text: string; isHashLink: boolean }) => (
                <ListItem key={to} style={{ whiteSpace: "nowrap" }}>
                  <PixyNavLink
                    isHashLink={isHashLink}
                    to={`/${!isHashLink ? "" : "#"}${to}`}
                  >{text}</PixyNavLink>
                </ListItem>
              )
            )}
          </List>
        </CenterSection>
        <RightSection>
          <Wallet />
          <PixyButton
            onClick={() => history.push("/create")}
            navButton
            fullWidth
            endIcon={<ArrowForwardRoundedIcon />}
          >
            Launch Project
          </PixyButton>
        </RightSection>
      </Toolbar>
    );
  }
  return (
    <>
      <AppBar
        color="transparent"
        sx={{
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          boxShadow: "none",
          height: 204,
          top: 0,
          left: 0
        }}
        position="absolute"
      >
        { mobileView ? displayMobile() : displayDesktop() }
      </AppBar>
      <BackgroundLayer layer={1} />
    </>
  );
};

export default Header;
