import { useLocation } from "react-router-dom";
import { PixyLink } from "./PixyLink";

export const PixyNavLink = ({ children, to, ...rest }: any) => {
  const { pathname } = useLocation();
  const routeIsActive = pathname === to;

  return (
    <PixyLink
      className={`NavLink${!routeIsActive ? "" : "-active"}`}
      to={to}
      color="secondary.dark"
      // underline={!routeIsActive ? "none" : undefined}
      underline="none"
      sx={{
        fontWeight: 600,
        letterSpacing: "1.2px",
        lineHeight: "24px"
        // ...(routeIsActive && {
        //   textUnderlineOffset: 8,
        //   textUnderlinePosition: "under",
        //   textDecorationColor: "#D13677"
        // })
      }}
      {...rest}
    >
      {children}
    </PixyLink>
  );
};
