import React, { lazy, Suspense } from "react";
import { Route, Switch } from "react-router-dom";
import { CreateCampaign } from "./components/CreateCampaign";
import { generatePath } from "react-router";

// Lazy load routes to decrease bundle size.
const Home = lazy(() => import("./pages/Home"));
const Catalog = lazy(() => import("./pages/Catalog"));
const HowItWorks = lazy(() => import("./pages/homePageSections/HowItWorks"));
const About = lazy(() => import("./pages/About"));
const CampaignNFT = lazy(() => import("./pages/CampaignNFT"));

export const namedRoutes = {
  campaignPath: (params: { campaignSlug: string }): string => {
    return generatePath("/campaign/:campaignSlug", params);
  },
  campaignNFTPath: (params: {
    campaignSlug: string;
    nftSlug: string;
  }): string => {
    return generatePath("/campaign/:campaignSlug/nft/:nftSlug", params);
  },
};

export const Routes = () => {
  return (
    <Suspense fallback={"loading"}>
      <Switch>
        <Route exact path="/" component={Home} />
        <Route
          exact
          path="/campaign/:campaignSlug/nft/:nftSlug"
          component={CampaignNFT}
        />
        <Route
          exact
          path="/create"
          component={() => <CreateCampaign>{}</CreateCampaign>}
        />
        <Route exact path="/catalog" component={Catalog} />
        <Route exact path="/how-it-works" component={HowItWorks} />
        <Route exact path="/about" component={About} />
      </Switch>
    </Suspense>
  );
};
