// @ts-nocheck
import React, { forwardRef } from "react";
import { HashLink } from "react-router-hash-link";

import { Link as RouterLink, LinkProps } from "react-router-dom";
import Link from "@mui/material/Link";

const ComposedLink = forwardRef<any, Omit<LinkProps, "to">>(({
                                                               isHashLink,
                                                               ...props
                                                             }, ref) => (
  <>
    {!isHashLink ? (
      <RouterLink ref={ref} {...props} />
    ) : (
      <HashLink
        ref={ref}
        scroll={(el) => el.scrollIntoView({ behavior: "smooth" })}
        {...props}
      />
    )}
  </>
));

/**
 * Custom component which combines the functionality of react-router's
 * `Link` component with the styling from material-ui's `Link`.
 */
export const PixyLink = ({ children, to, ...rest }: any) => {
  return (
    <Link to={to} component={ComposedLink} {...rest}>
      {children}
    </Link>
  );
};
