import React, { ReactNode } from "react";
import Box from "@mui/material/Box";
import backImage from "../../assets/images/body-back.png";

export interface MainLayoutProps {
  children: ReactNode;
}

export const MainLayout = ({ children }: MainLayoutProps) => {
  return (
    <Box
      component="main"
      sx={{
        filter: "drop-shadow(0px 4px 14px rgba(0, 0, 0, 0.8))"
      }}
    >
      <Box
        sx={{
          background: "linear-gradient(90deg, rgba(0, 0, 0, 0.5) 11.6%,rgba(0, 0, 0, 0) 52.99%)",
          backgroundBlendMode: "darken"
        }}
      >
        <Box
          sx={{
            background: "linear-gradient(81.42deg, rgba(255, 0, 199, 0.6) 0.43%, rgba(0, 240, 255, 0.6) 97.99%)",
            backgroundBlendMode: "multiply"
          }}
        >
          <Box
            sx={{
              background: "#000"
            }}
          >
            <Box
              sx={{
                display: "flex",
                paddingTop: "128px",
                background:
                  `linear-gradient(81.42deg, rgba(255, 0, 199, 0.6) 0.43%,rgba(0, 240, 255, 0.6) 97.99%),url(${backImage})`,
                backgroundBlendMode: "multiply",
                backgroundSize: "cover",
                minHeight: "100vH",
                width: "100%",
                justifyContent: "center",
                ['@media(min-width: 1700px)'] : {
                  marginTop: "-30px",
                },
                ['@media(min-width: 1800px)'] : {
                  marginTop: "-60px",
                }
              }}
            >
              <Box
                sx={{
                  flexGrow: 1,
                  flexDirection: "column",
                  display: "flex"
                }}
              >
                {children}
              </Box>
            </Box>
          </Box>
        </Box>
      </Box>
    </Box>
  );
};
