import React from "react";

const layerStyles = (layer: number) => {
  switch (layer) {
    case 1:
      return {
        opacity: 0.6,
        MixBlendMode: "multiply",
        background: "linear-gradient(180deg, #000000 0%, rgba(0, 0, 0, 0) 100%)"
      };
    case 2:
      return {
        background: "linear-gradient(81.42deg, rgba(255, 0, 199, 0.6) 0.43%,rgba(0, 240, 255, 0.6) 97.99%)",
        backgroundBlendMode: "multiply"
      };
  }
};
export const BackgroundLayer = ({ layer }: { layer: number }) => {
  return (
    <div
      style={{
        ...layerStyles(layer),
        position: "absolute",
        width: "100%",
        height: 204,
        top: 0,
        left: 0,
        zIndex: layer
      }}
    />
  );
};
