import React from "react";
import { Button } from "@mui/material";

export const PixyButton = ({
                             children,
                             navButton = false,
                             sx = {},
                             upperCase = true,
                             variant = "contained",
                             kind = "primary",
                             ...rest
                           }: any) => {
  return (
    <Button
      disableRipple
      disableElevation
      sx={{
        ...sx,
        fontWeight: navButton ? undefined : 400,
        padding: "15px 25px",
        letterSpacing: "1.2px",
        height: 52,
        ...(variant === "outlined" && {
          borderColor: "#FFF",
          color: "#FFF",
          "&:hover": {
            borderColor: "#FFF"
          }
        }),
        ...(!upperCase && {
          textTransform: "capitalize"
        }),
        ...(!navButton && variant !== "outlined" && {
          background: "linear-gradient(360deg, rgba(0, 0, 0, 0.2) 0%,"
            + " rgba(0, 0, 0, 0) 100%), #D13677",
          boxShadow: "0px 4px 4px rgba(0, 0, 0, 0.25), inset 0px 1px 1px rgba(255, 255,255, 0.1)"
        }),
        ...(navButton && {
          mx: 2,
          background: "linear-gradient(360deg, rgba(0, 0, 0, 0.2) 0%,rgba(0, 0, 0, 0) 100%), #D13677",
          boxShadow: "0px 1px 2px rgba(0, 0, 0, 0.5), inset 0px 1px 1px rgba(255, 255, 255, 0.25)"
        }),
        ...(kind === "secondary" && {
          background: "linear-gradient(360deg, rgba(0, 0, 0, 0.1) 0%, rgba(0, 0, 0, 0) 100%), linear-gradient(0deg, #3C3C3C, #3C3C3C)",
          borderRadius: "5px",
          textTransform: "capitalize"
        })
      }}
      variant={variant}
      {...rest}
    >
      {children}
    </Button>
  );
};
